/* 전체 */
body {
  position: relative;
}

#wrapper {
  opacity: 0;
  width: 100%;
  min-width: 1400px;
  overflow: hidden;
}
#wrapper.on {
  opacity: 1;
}

.wrap_cen {
  clear: both;
  position: relative;
  margin: 0 auto;
  width: 1400px;
  height: 100%;
}

*::selection {
  background: #ffeaf6;
  color: #6b007d;
}

.bg_el {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.bg_el span {
  position: absolute;
  top: 0;
  left: 0;
}
.bg_el img {
  position: relative;
}

.c_blue {
  color: #2a93e9;
  font-style: normal;
}

/* 헤더 */
.page_hd {
  position: fixed;
  top: 10px;
  padding: 0 15px;
  box-sizing: border-box;
  z-index: 99999;
}
.page_hd .wrap_cen {
  display: flex;
  max-width: 1400px;
  margin: 0 auto;
  padding: 2px 20px;
  box-sizing: border-box;
  border-radius: 25px;
  background: transparent;
  transition: 0.3s all 0s cubic-bezier(0, 0, 0, 0.64);
}
.page_hd .logo {
  visibility: hidden;
  width: 50px;
  padding: 8px 0;
  box-sizing: border-box;
}
.page_hd .logo a {
  display: block;
}
.page_hd .logo img {
  width: 100%;
}
.page_hd .gnb_wrap,
.page_hd #gnb_pc {
  display: flex;
  flex: 1;
}
.page_hd .gnb_menu {
  flex: 1;
}
.page_hd .gnb_menu ul {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}
.page_hd .gnb_menu li {
  padding: 0 0.3%;
}
.page_hd .gnb_menu li a {
  display: block;
  padding: 5px;
  font-size: 13px;
  color: #eee;
}
.page_hd .gnb_menu li.on a {
  color: #ffff00;
}
.page_hd.visible .wrap_cen {
  background: rgba(255, 255, 255, 0.95);
}
.page_hd.visible .logo {
  visibility: visible;
}
.page_hd.visible #gnb_pc .gnb_menu a {
  color: #333;
}
.page_hd.visible #gnb_pc .gnb_menu li.on a {
  color: #ff229f;
}
.pf_on .page_hd {
  display: none;
}

/* 섹션공통 */
section {
  clear: both;
  position: relative;
  padding: 90px 0;
  min-height: 750px;
  box-sizing: border-box;
  /* 콘텐츠 섹션 공통 - 메뉴 */
}
section:not(#history):not(#portfolio) {
  height: 100vh;
}
section #portfolio {
  min-height: 100vh;
}
section > .sec_t {
  position: relative;
  color: #fff;
  text-align: center;
  font-size: 50px;
  line-height: 42px;
  animation: toT 0.7s cubic-bezier(0.4, 0, 0.2, 1);
  top: -30px;
  opacity: 0;
  z-index: 1;
}
section h2 + .set_ct {
  position: relative;
  margin-top: 10px;
  text-align: center;
  color: #fff;
  line-height: 18px;
  animation: toT 0.7s cubic-bezier(0.4, 0, 0.2, 1);
  top: -30px;
  opacity: 0;
  transition: 0.3s opacity 0s linear;
}
section.active > .sec_t {
  animation: fromT 0.7s cubic-bezier(0.4, 0, 0.2, 1);
  animation-fill-mode: backwards;
  top: 0;
  opacity: 1;
}
section.active > .sec_t:after {
  animation: fromT 0.7s cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
  animation-fill-mode: backwards;
  top: 0;
  opacity: 1;
}
section.active h2 + .set_ct {
  animation: fromT 0.7s cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
  animation-fill-mode: backwards;
  top: 0;
  opacity: 0.9;
  z-index: 9;
}

