@keyframes fromL {
  0% {
    opacity: 0;
    right: 15px;
  }
  100% {
    opacity: 1;
    right: 0;
  }
}
@keyframes fromR {
  0% {
    opacity: 0;
    left: 15px;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}
@keyframes fromT {
  0% {
    opacity: 0;
    top: -15px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
@keyframes fromB {
  0% {
    opacity: 0;
    top: 15px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
@keyframes toL {
  100% {
    opacity: 0;
    right: 15px;
  }
  0% {
    opacity: 1;
    right: 0;
  }
}
@keyframes toR {
  100% {
    opacity: 0;
    left: 15px;
  }
  0% {
    opacity: 1;
    left: 0;
  }
}
@keyframes toT {
  100% {
    opacity: 0;
    top: -15px;
  }
  0% {
    opacity: 1;
    top: 0;
  }
}
@keyframes toB {
  100% {
    opacity: 0;
    top: 15px;
  }
  0% {
    opacity: 1;
    top: 0;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
