.form-item {
  opacity: 0;
  position: relative;
  top: 30px;
  text-align: center;
  border-radius: 12px;
  overflow: hidden;
  animation: toB 0.7s cubic-bezier(0.4, 0, 0.2, 1);
}
.form-item.radioItem {
  display: inline-block;
  width: 134px;
  border: 1px solid #affdfb;
}
.form-item.radioItem label {
  border-top: 3px solid transparent;
}
.form-item.radioItem label:focus {
  border-top: 3px solid #fff;
}
.form-item.radioItem input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.form-item.radioItem input ~ label {
  width: 100%;
  padding: 5px 0 10px 0;
}
.form-item.radioItem input:checked ~ label {
  border-top: 3px solid #fff;
}
.form-item.radioItem.radioItem {
  margin-left: 4px;
}
.form-item.radioItem2 input ~ label, .form-item.chkItem input ~ label {
  position: relative;
  line-height: 1;
}
.form-item.radioItem2 input ~ label:before, .form-item.chkItem input ~ label:before {
  content: "";
  display: inline-block;
  margin-right: 8px;
  width: 22px;
  height: 22px;
  vertical-align: middle;
  border: 1px solid #affdfb;
}
.form-item.radioItem2 input ~ label:after, .form-item.chkItem input ~ label:after {
  content: "";
  opacity: 0;
  display: block;
  position: absolute;
  top: 4px;
  left: 4px;
  width: 16px;
  height: 16px;
  transform: scale(0);
  transition-property: transform, opacity;
  transition: 0.08s cubic-bezier(0.51, -0.19, 0.17, 0.82);
}
.form-item.radioItem2 input:checked ~ label, .form-item.chkItem input:checked ~ label {
  color: #fff;
}
.form-item.radioItem2 input:checked ~ label:before, .form-item.chkItem input:checked ~ label:before {
  border-color: #fff;
}
.form-item.radioItem2 input:checked ~ label:after, .form-item.chkItem input:checked ~ label:after {
  background-color: #fff;
  opacity: 1;
  transform: scale(1);
}
.form-item.radioItem2:nth-child(n+1), .form-item.chkItem:nth-child(n+1) {
  margin-left: 15px;
}
.form-item.radioItem2 {
  display: inline-block;
}
.form-item.radioItem2 label:focus {
  border-top: 3px solid #fff;
}
.form-item.radioItem2 input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.form-item.radioItem2 input ~ label:before, .form-item.radioItem2 input ~ label:after {
  border-radius: 50%;
}
.form-item.chkItem {
  display: block;
}
.form-item.chkItem input ~ label:before, .form-item.chkItem input ~ label:after {
  border-radius: 5px;
}
.form-item input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.form-item input:checked ~ label {
  color: #fff;
}
.form-item label {
  display: inline-block;
  color: #affdfb;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.3s all 0s cubic-bezier(0.4, 0, 0.2, 1);
}
.form-item label:hover, .form-item label:focus {
  color: #fff;
}
.form-item:nth-child(1) {
  animation-delay: 0.5s;
}
.form-item:nth-child(2) {
  animation-delay: 0.4s;
}
.form-item:nth-child(3) {
  animation-delay: 0.3s;
}
.form-item:nth-child(4) {
  animation-delay: 0.2s;
}
.form-item:nth-child(5) {
  animation-delay: 0.1s;
}
.form-item:nth-child(6) {
  animation-delay: 0s;
}

